'use strict';

import { render } from 'react-dom';
import {IS_LEGACY_DASHBOARD} from "./config";
import AppLegacy from './components/legacy/Dashboard.jsx';
import App from './components/Dashboard.jsx';

if ( IS_LEGACY_DASHBOARD ) {
	render( <AppLegacy />, document.getElementById( 'yith-wcmv-dashboard-root' ) );
} else {
	render( <App />, document.getElementById( 'yith-wcmv-dashboard-root' ) );
}
